import { Component } from '@angular/core';
import { NavController, LoadingController, AlertController } from 'ionic-angular';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularfireProvider } from '../../providers/angularfire/angularfire';
import { UserPage } from '../user/user';
import { LoginPage } from '../login/login';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  users : any[];
  myUser = this.afAuth.auth.currentUser;
  uid = this.myUser.uid;
  loadingContent: string;
  loadingAlert: any;
  public photo = ProfilePhoto.profilePhotoDefault

  constructor(
    public navCtrl: NavController,
    private afAuth: AngularFireAuth,
    private afDb: AngularFireDatabase,
    private afProvider: AngularfireProvider,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
    ) {
      console.log(this.uid);
      this.loadingContent='Obteniendo datos de usuarios'
      this.loading(this.loadingContent);
  }

  ionViewDidEnter() {
    
    this.afProvider.getUsersList().valueChanges().subscribe(users=>{
      this.users=users;
      //console.log(this.users);
      if(this.users){
        this.loadingAlert.dismiss();
        console.log(this.users.length)
        for (var n = 0; n <= this.users.length; n++){
          console.log(this.users[n]);
          if(this.users[n].User_Info.profilePhoto === undefined){
            this.users[n].User_Info.profilePhoto = this.photo
          };
          if(n===this.users.length-1){
            console.log(n+'Final');
            break
          }
        }
      }
    })
  };

  toUserDataBase(uid, nickName, run, photo){
    console.log(uid, nickName, run)
    this.navCtrl.push(UserPage, {uid:uid, nickName:nickName, run:run, photo:photo})
  }

  logout(){
    const alert = this.alertCtrl.create({
      title: 'Confirme',
      message: '¿Realmente desea salir de la sesión?',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Ok',
          handler: () => {
            console.log('Buy clicked');
            this.signOut();
          }
        }
      ]
    });
    alert.present();
  }
  
  signOut(){
    this.loadingContent='Cerrando Sesión'
    this.loading(this.loadingContent);
    this.afAuth.auth.signOut().then(()=>{
      this.navCtrl.setRoot(LoginPage).then(()=>{
        this.loadingAlert.dismiss();
      })
    })
  }

  loading(loadingContent: string){
    this.loadingAlert = this.loadingCtrl.create({
        content: loadingContent,
      });
      this.loadingAlert.present();
  }

}

export class ProfilePhoto{
  static profilePhotoDefault = 'data:image/png;base64,'
  
}



